
import { defineComponent, ref } from 'vue';
import { QuickData, QuickFetch } from '@/quick';
import { useRoute } from 'vue-router';
import SmallModal from '@/components/SmallModal.vue';

export default defineComponent({
  setup() {
    // quick
    const qf = new QuickFetch();
    const qd = new QuickData();
    const route = useRoute();
    const displayModal = ref(0);
    const selected: any = ref({});
    // lists
    const registerList: any = ref([]);
    function getRegister() {
      qf.Url(`register/ind/list/${route.params.gameCode}/${route.params.gameId}`).GetAll(registerList);
    }
    getRegister();
    return {
      registerList,
      displayModal,
      selected,
      openMember: (input: any) => {
        selected.value = input;
        displayModal.value = 1;
      },
    };
  },
  components: {
    SmallModal,
  },
});
